#toggle:checked {
  // Открываем
  & ~ .header__menu {
    // Восстанавливаем пункты навигации из «линий» в иконке меню
    div {
      width: 0;
      height: auto;
      margin-bottom: -32vh;
      display: flex;
      justify-content: center;
      background-color: white;
      border-radius: 100%;

      & * {
        transition: 0.5s, opacity 0.1s;
      }

      .header__navBtn {
        color: #45515F;
        background-color: wheat;
        letter-spacing: 0;
        height: 40px;
        line-height: 40px;
        width: 96vw;
        margin-top: 0;
        margin-left: calc(0.05em + (-97vw));
        opacity: 1;
        transform: scaleY(1);
        box-shadow: inset 0 0 20px black;
        border-radius: 8px;
        //последовательное появления
        $items: 3;
        $transition-delay: -0.05s;

        @for $i from 1 through $items {
          &:nth-child(#{$i}) {
            $delay: ($i - 1) * $transition-delay;
            transition-delay: $delay;
            &:before {
              transition-delay: $delay;
            }
          }
        }
        
        // Скрываем линии
        &:before {
          opacity: 0;
        }
      }
    }
  }
}